.devquarium-main-div{
    display: flex;
    flex-direction: row;
  border-radius: 20px;
  position: relative;
  text-align: left;
  overflow-x: scroll;
  background-color: rgb(1, 8, 32);

  height: 80vh;
  width: 80vw;
  font: 1.3rem Inconsolata, monospace;
  text-shadow: 0 0 5px #C8C8C8;
  align-self: center;
}
.devquarium-side-panel{
    align-content: center;
    width: 25%;
    font: 1.3rem Roboto Mono;
}
.welcome{
    padding-top: 20%;
    text-align: center;
    margin: auto;
    width: 90%;
}
.welcome-title{
    font-family: Atlantis;
    font-size: 2.5vw;
    width: 100%;
}

.project-intro{
    padding-top: 20%;
    margin: auto;
    width: 80%;
    text-align: justify;
}

.enjoy-message{
    padding-top: 20%;
    margin: auto;
    width: 80%;
    text-align: justify;
}

.devquarium-tank{
    /* margin: 1.5%; */
    position: relative;
    background-image: url('./images/underwater-background-busy.gif');
    /* background-position: center; */
    background-size: 80vh;
    background-repeat: no-repeat;
    height: 80vh;
    min-width: 40vw;
    overflow: hidden;
}

/* individiual gif styling */
.border{
    position: absolute;
    top: -5vh;
    left: -7vw;
    height: 80vh;
    width: 55vw;
    background-position-y: top;
    z-index: 1000;
    /* width: 100%; */
    /* transform: rotate(90deg) */
}

.axolotl{
    position: absolute;
    width: 8vw;
    z-index: 2;
    animation-name: axolotl-swim-1, axolotl-swim-2, axolotl-swim-3;
    animation-duration: 30s, 30s, 30s;
    animation-delay: 0s, 30s, 60s;
    animation-iteration-count: infinite;
}

@keyframes axolotl-swim-1 {
    0% {left: -20%; top: 20%;}
    30% {left: -20%; top: 20%;}
    70% {left: 65%; top: 60%;}
    100% {left: 140%; top: 50%;}
}
@keyframes axolotl-swim-2 {
    0% {left: 110%; top: 30%; transform: scaleX(-1)}
    50% {left: 60%; top: 70%; transform: scaleX(-1)}
    100% {left: -20%; top: 80%;transform: scaleX(-1)}
}
@keyframes axolotl-swim-3 {
    0% {left: 40%; top: 110%; transform: scaleX(1);}
    25% {left: 40%; top: 70%; transform: scaleX(1)}
    50% {left: 60%; top: 65%; transform: scaleX(1)}
    60% {left: 70%; top: 60%; transform: scaleX(1)}
    65% {left: 90%; top: 65%; transform: scaleX(-1)}
    70% {left: 85%; top: 70%; transform: scaleX(-1)}
    75% {left: 75%; top: 65%; transform: scaleX(1)}
    80% {left: 85%; top: 70%; transform: scaleX(1)}
    100% {left: 130%; top: 80%;transform: scaleX(1)}
}

.scary-fish {
    position: absolute;
    width: 20vw;
    left: -20vw;
    z-index: 100;
    animation-name: scary-swim-1, scary-swim-2, scary-swim-3;
    animation-duration: 7s, 30s, 30s;
    animation-delay: 23s, 30s, 60s;
    animation-iteration-count: infinite;
}

@keyframes scary-swim-1{
    0% {left: -35%; top: 10%;}
    100% {left: 120%; top: 50%;}
}
@keyframes scary-swim-2{
    0% {left: 110%; top: 0%; transform: scaleX(-1)}
    10% {left: 100%; top: 0%; transform: scaleX(-1)}
    20% {left: 70%; top: 0%; transform: scaleX(-1)}
    30% {left: 65%; top: 0%; transform: scaleX(-1)}
    35% {left: 60%; top: 0%; transform: scaleX(-1)}
    40% {left: 50%; top: 0%; transform: scaleX(-1)}
    50% {left: 40%; top: 0%; transform: scaleX(-1)}
    60% {left: 50%; top: 0%; transform: scaleX(-1)}
    65% {left: 40%; top: 0%; transform: scaleX(-1)}
    70% {left: 59%; top: 0%; transform: scaleX(-1)}
    80% {left: 69%; top: 0%; transform: scaleX(-1)}
    90% {left: 95%; top: 0%; transform: scaleX(-1)}
    100% {left: 115%; top: 0%; transform: scaleX(-1)}
}
@keyframes scary-swim-3{
    0% {left: -80%; top: -30%; transform: scaleX(1)}
    50% {left: -80%; top: -30%; transform: scaleX(1)}
    70% {left: -30%; top: 0%; transform: scaleX(1)}
    80% {left: -25%; top: 8%; transform: scaleX(1)}
